import { useEffect, useState } from 'react';
import { Box, Typography } from "@mui/material";
import useAuth from "../hooks/useAuth";
import styled from "styled-components";
import Topbar from "../Components/Topbar";
import { useAppDispatch, useAppSelector } from '../app/hooks';
import { getderiAmtInWallet, getbnbAmtInWallet, setderiAmtInWallet, setbnbAmtInWallet, getliquidityCaption } from '../actions/ToggleMode';
import { swapEthtoDeri, swapDeritoEth, tokenApprove, getBNB, getBalance } from '../hooks/useTokenInfo';
import { SWAP_CONTRACT_ADDR, DERI_TOKEN_ADDR } from '../hooks/tokenAddress';
import axios from 'axios';

const Liquidity = () => {
    const { account } = useAuth();
    const deriDecimal = 18;

    const [bnbAmount, setbnbAmount] = useState('');
    const [deriAmount, setderiAmount] = useState('');

    const deriAmt = useAppSelector(getderiAmtInWallet);
    const bnbAmt = useAppSelector(getbnbAmtInWallet);
    const liquidityCaption = useAppSelector(getliquidityCaption);

    const dispatch = useAppDispatch();

    const onChangeAmount = (value: string, amtType: string) => {
        if (amtType === 'bnb') {
            setbnbAmount(value);
        } else if (amtType === 'deri') {
            setderiAmount(value);
        }
    }

    useEffect(() => {
        console.log('start axios')
        const tokenName = 'binancecoin';
        let url = 'https://api.coingecko.com/api/v3/simple/price?ids=' + tokenName + '&vs_currencies=usd';
        axios.get(url).then(ret => {
            console.log(ret.data[tokenName]['usd']);
        });
    }, []);

    const onAddLiquidity = async () => {

    }

    return (
    <ParentBox>
        <Topbar mode="staking" />
        <ProtocolBox style={{position: 'absolute', bottom: 30, right: 30, zIndex: 1}}>
            <img src='img/deri-protocol.png' alt='protocol' />
        </ProtocolBox>
        <MainBox>
            <SwapBox style={{zIndex: 2}}>
                <Typography style={{fontSize: 30, fontWeight: 700}}>Add Liquidity</Typography>
                <Typography style={{fontSize: 14, fontWeight: 500, color: '#999999', marginBottom: 30}}>Receive LP Tokens and earn 0.17% trading fees</Typography>
                <Box style={{width: '100%', border: '1px solid #999999', marginBottom: 30}}></Box>
                <OneRow style={{paddingBottom: 0, paddingTop: 0, width: '100%'}}>
                    <OneRow style={{justifyContent: 'flex-start'}}>
                        <img src={'img/bnb.png'} style={{width: 32}} alt='sell token' />
                        <Box style={{marginLeft: 10, fontSize: 20, fontWeight: 600}}>BNB</Box>
                    </OneRow>
                    <OneRow style={{justifyContent: 'flex-end'}}>
                        <Typography>Balance:</Typography>
                        <Typography style={{marginLeft: 10, fontSize: 16, fontWeight: 700}}>{bnbAmt}</Typography>
                    </OneRow>
                </OneRow>
                <RoundBox>
                    <input value={bnbAmount} onChange={(event) => {onChangeAmount(event.target.value, 'sell')}} style={{fontSize: 16, fontWeight: 700, background: 'transparent', color: 'white', border: 'none', outline: 'none', padding: '5px 15px'}} placeholder='0.0'  />
                </RoundBox>
                <Box style={{fontSize: 40}}>+</Box>
                <OneRow style={{paddingBottom: 0, paddingTop: 0, width: '100%'}}>
                    <OneRow style={{justifyContent: 'flex-start'}}>
                        <img src={'img/deri.png'} style={{width: 32}} alt='buy token' />
                        <Box style={{marginLeft: 10, fontSize: 20, fontWeight: 600}}>DERI</Box>
                    </OneRow>
                    <OneRow style={{justifyContent: 'flex-end'}}>
                        <Typography>Balance:</Typography>
                        <Typography style={{marginLeft: 10, fontSize: 16, fontWeight: 700}}>{deriAmt}</Typography>
                    </OneRow>
                </OneRow>
                <RoundBox>
                    <input value={deriAmount} onChange={(event) => {onChangeAmount(event.target.value, 'buy')}} style={{fontSize: 16, fontWeight: 700, background: 'transparent', color: 'white', border: 'none', outline: 'none', padding: '5px 15px'}} placeholder='0.0'  />
                </RoundBox>
                <Box style={{justifyContent: 'center', border: '1px solid #555555', borderRadius: 12, width: '100%', padding: '10px 0 0', marginTop: 20}}>
                    <Typography style={{padding: '10px 0', marginLeft: 10}}>Prices and Pool Share</Typography>
                    <Box style={{border: '1px solid #555555', borderRadius: 12, width: '100%'}}>
                        <OneRow>
                            <Box>
                                <Typography style={{color: '#22aa99'}}>67.3845</Typography>
                                <Typography>DERI per BNB</Typography>
                            </Box>
                            <Box>
                                <Typography style={{color: '#22aa99'}}>0.014844</Typography>
                                <Typography>BNB per DERI</Typography>
                            </Box>
                        </OneRow>
                        <Box style={{marginLeft: 10, marginBottom: 10}}>
                            <Typography style={{color: '#22aa99', textAlign: 'center'}}>0%</Typography>
                            <Typography style={{textAlign: 'center'}}>Share of Pool</Typography>
                        </Box>
                    </Box>
                </Box>
                <BlueButton onClick = {() => onAddLiquidity()}>{liquidityCaption}</BlueButton>
            </SwapBox>
        </MainBox>
    </ParentBox>
    )
}

const ProtocolBox = styled(Box)`
    >img {
        @media (max-width: 500px) {
            width: 300px;
        }
    }
`;
const BlueButton = styled(Box)`
    border-radius: 12px;
    background: rgb(1, 119, 251);
    width: -webkit-fill-available;
    text-align: center;
    padding: 10px 15px;
    font-size: 20px;
    font-weight: 500;
    :hover {
        color: rgb(1, 119, 251);
        background: white;
        cursor: pointer;
    }
    margin-top: 70px;
`;
const RoundBox = styled(Box)`
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-radius: 12px;
    background: #444444;
    width: -webkit-fill-available;
    >input {
        width: 100%;
        text-align: right;
    }
`;
const OneRow = styled(Box)`
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
`;
const SwapBox = styled(Box)`
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgb(44, 45, 58);
    padding: 20px;
`;
const MainBox = styled(Box)`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgb(26,27,35);
    // margin-top: 100px;
    // margin-bottom: 100px;
    height: 100%;
    position: relative;
`;
const ParentBox = styled(Box)`
    display: flex;
    flex-direction: column;
    padding-top: 40px;
    align-items: center;
    justify-content: space-between;
    width: -webkit-fill-available;
    height: 100%;
    background: rgb(26, 27, 35);
`;

export default Liquidity;